<template>
<body>
  <div class="container">
    <h2>用户反馈</h2>
    <form @submit.prevent="submitForm">
      <div class="form-group">
        <label for="feedbackType">反馈类型</label>
        <select id="feedbackType" v-model="formData.type" required>
          <option value="">请选择反馈类型</option>
          <option value="问题报告">问题报告</option>
          <option value="功能建议">功能建议</option>
          <option value="界面建议">界面建议</option>
          <option value="活动建议">活动建议</option>
        </select>
      </div>
      <div class="form-group">
        <label for="feedbackContent">反馈内容</label>
        <textarea id="feedbackContent" v-model="formData.message" rows="6" required></textarea>
      </div>
      <div class="button-group">
        <button type="submit">提交</button>
        <button type="reset">重置</button>
      </div>
    </form>
  </div>
</body>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      formData: {
        type: '',
        message: '',
        date: ''
      }
    };
  },
  methods: {
    async submitForm() {
      if (!this.formData.type || this.formData.type.trim() === '') {
        this.$alert('请选择反馈类型', '错误', {
          confirmButtonText: '确定',
          type: 'error'
        });
        return;
      }
      if (!this.formData.message || this.formData.message.trim() === '') {
        this.$alert('请输入反馈内容', '错误', {
          confirmButtonText: '确定',
          type: 'error'
        });
        return;
      }
      this.formData.date = new Date().toISOString(); // 使用 ISO 格式

      try {
        const response = await axios.post('http://localhost:8090/feedback', this.formData, {
          headers: {
            'Content-Type': 'application/json'
          }
        });
        console.log('提交成功:', response.data);
        this.showSuccessAlert(); // 显示成功提示弹窗
      } catch (error) {
        console.error('提交失败:', error);
        this.showErrorAlert(); // 显示失败提示弹窗
      }
    },
    showSuccessAlert() {
      this.$alert('提交成功！感谢你的反馈！', '成功', {
        confirmButtonText: '确定',
        type: 'success'
      });
    },
    showErrorAlert() {
      this.$alert('提交失败，请重试！', '错误', {
        confirmButtonText: '确定',
        type: 'error'
      });
    }
  }
};
</script>

<style scoped>
body {
  background: linear-gradient(135deg, #ff7e5f, #feb47b); /* 渐变背景 */
  margin: 0;
  font-family: 'Comic Sans MS', cursive, sans-serif; /* 有趣的字体 */
  height: 100vh; /* 使背景颜色平铺整个屏幕 */
}

.container {
  max-width: 600px;
  margin: 50px auto; /* 上下间距 */
  padding: 30px; /* 增加内边距 */
  background-color: rgba(255, 255, 255, 0.9); /* 半透明白色背景 */
  border-radius: 20px; /* 圆角 */
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2); /* 更强的阴影效果 */
  animation: bounce 1s infinite; /* 添加弹跳动画 */
  
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

h2 {
  text-align: center;
  color: #ff4757; /* 标题颜色 */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3); /* 文字阴影 */
}

.form-group {
  margin-bottom: 20px;
}

input, select, textarea {
  width: 100%;
  padding: 12px;
  box-sizing: border-box;
  border: 2px solid #ff4757; /* 边框颜色 */
  border-radius: 10px;
  font-size: 16px; /* 字体大小 */
  transition: border-color 0.3s; /* 边框颜色变化 */
}

input:focus, select:focus, textarea:focus {
  border-color: #ff6348; /* 聚焦时的边框颜色 */
}

button {
  padding: 12px 24px;
  background-color: #ff4757;
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  margin-right: 10px; /* 按钮之间的间距 */
  font-size: 16px; /* 字体大小 */
  transition: background-color 0.3s; /* 背景颜色变化 */
}

button:hover {
  background-color: #ff6348; /* 悬停时的背景颜色 */
}

.contact-info {
  margin-top: 20px; /* 联系信息与表单的间距 */
  text-align: center;
  color: #555; /* 联系信息颜色 */
}
</style>
